import { useEffect, useState } from 'react'
import { TextField } from '@material-ui/core'
import { CheckItem } from '../models/CheckItem'
import {
  Checkbox,
  ListItemButton,
  ListItemIcon,
  ListItemText,
} from '@mui/material'

//property
interface Props {
  tableItem: CheckItem[]
}

//props
export function SearchView({ tableItem }: Props) {
  const notTableItems: CheckItem = {
    name: 'No Item Found',
    price: 0,
    checked: false,
  }

  //
  const [keyword, setKeyword] = useState('')
  // tableItem
  const [filteredTableItems, setFilteredTableItems] = useState(tableItem)

  // ""(setFilteredProducts)products
  useEffect(() => {
    if (keyword === '') {
      setFilteredTableItems(tableItem)
      return
    }

    // 
    const searchKeywords = keyword
      //
      .trim()
      //
      .toLowerCase()
      // g = 
      .match(/[^\s]+/g)

    //
    if (searchKeywords === null) {
      setFilteredTableItems(tableItem)
      return
    }

    const result = tableItem.filter((product) =>
      searchKeywords.every(
        (kw) => product.name.toLowerCase().indexOf(kw) !== -1
      )
    )

    setFilteredTableItems(result.length ? result : [notTableItems])
  }, [keyword])

  const [render, setRender] = useState(true)

  return (
    <>
      <TextField
        //
        id='field'
        color='secondary'
        variant='outlined'
        label=''
        onChange={(e) => setKeyword(e.target.value)}
      />
      {filteredTableItems.map((item) => {
        //checked
        const handleToggle = () => {
          item.checked = !item.checked
          setRender(!render)
        }
        return (
          <ListItemButton role={undefined} onClick={handleToggle} dense>
            <ListItemIcon>
              <Checkbox
                edge='start'
                checked={item.checked}
                tabIndex={-1}
                disableRipple
              />
            </ListItemIcon>
            <ListItemText primary={item.name} />
            <ListItemText
              style={{ display: 'flex', justifyContent: 'flex-end' }}
              primary={item.price + ' '}
            />
          </ListItemButton>
        )
      })}
    </>
  )
}
