import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
} from '@mui/material'
import { CheckItem } from '../../models/CheckItem'
import { SearchView } from '../../components/SearchView'

//
type hF = () => void
type uF = (newItems: CheckItem[]) => void
type usF = (newItems: CheckItem[]) => void

//property
interface Props {
  open: boolean
  tableItem: CheckItem[]
  handleClose: hF
  upDate: uF
  upDateSum: usF
}
//props
export function SearchDialog({
  open,
  handleClose,
  tableItem,
  upDate,
  upDateSum,
}: Props) {
  return (
    <Dialog
      sx={{ '& .MuiDialog-paper': { width: '100%', maxHeight: 600 } }}
      maxWidth='xs'
      open={open}
      //DialogonClickhandleClose
      onClose={handleClose}
    >
      <DialogTitle></DialogTitle>
      <DialogContent dividers>
        <SearchView
          //props
          tableItem={tableItem}
        />
      </DialogContent>
      <DialogActions>
        <Button
          //true(props)HometableItem
          onClick={() => {
            upDate(tableItem)
            handleClose()
            upDateSum(tableItem)
          }}
        >
          
        </Button>
      </DialogActions>
    </Dialog>
  )
}
